<template>
  <VNavigationDrawer v-model="app.drawer">
    <template v-slot:prepend>
      <VList>
        <VListItem
          :prepend-avatar="auth.userAvatar"
          :title="auth.userName"
          :subtitle="auth.userEmail"
        />
      </VList>
    </template>

    <VDivider />

    <SDrawerNav />

    <template v-slot:append>
      <div class="pa-2">
        <v-btn to="/logout" color="logout-btn" block> Logout </v-btn>
        <div class="text-center">
          <small> Last login </small>
          <small> {{ lastLogin }} </small>
        </div>
      </div>
    </template>
  </VNavigationDrawer>
</template>
<script setup>
import { useAppStore } from '@/stores/appStore'

let app = useAppStore()
let auth = useAuthStore()

const lastLogin = auth.user.metadata.lastSignInTime
</script>
